.news-hero-carousel {
  --hero-h: clamp(260px, 48vw, 420px);
}
.news-hero-carousel .carousel-item img {
  height: var(--hero-h);
  object-fit: cover;
}
.news-hero-carousel .carousel-gradient {
  position: absolute;
  inset: 0;
  background: linear-gradient(180deg, rgba(0,0,0,0.1), rgba(0,0,0,0.55) 65%);
}
.news-hero-carousel .carousel-caption {
  position: absolute;
  left: 2rem;
  right: 2rem;
  bottom: 1.6rem;
  text-shadow: 0 2px 10px rgba(0,0,0,.45);
}
.news-hero-carousel h2 {
  color: #fff;
}
.news-hero-carousel p {
  color: rgba(255,255,255,.9);
}

/* ===== About Page ===== */
.about-hero { margin: 10px auto 24px; }
.about-intro .about-card{
  background: rgba(255,255,255,0.04);
  border: 1px solid rgba(255,255,255,0.06);
  border-radius: 16px;
}
.about-intro .about-photo{
  height: clamp(220px, 40vw, 360px);
}

.fw-extrabold { font-weight: 800; }
.about-underline{
  width: 180px;
  height: 4px;
  background: linear-gradient(90deg,#1fbfff,#5e8bff);
  border-radius: 10px;
}

.about-mini{
  border-radius: 14px;
  background: rgba(255,255,255,0.04);
  border: 1px solid rgba(255,255,255,0.06);
}

.card-dark{
  background: rgba(255,255,255,0.03);
  border: 1px solid rgba(255,255,255,0.06);
  border-radius: 16px;
}
